import MenuBlock from "@/components/MenuBlock/index.vue";
import MenuBlockPlaceHolder from "@/components/PlaceHolders/MenuBlockPlaceHolder/index.vue";
import "vue-skeletor/dist/vue-skeletor.css";
import { defineComponent } from "vue";
import { Block } from "@/interfaces/Block";
import EventEmitter from "@/util/EventEmitter";

const items: Block[] = [];

export default defineComponent({
  name: "BlocksMenuComponent",
  props: {
    blocks: Object,
    show: Boolean,
  },
  components: {
    MenuBlock,
    MenuBlockPlaceHolder,
  },
  watch: {
    blocks: {
      handler(val) {
        this.items = val;
        this.filteredItems = val;
        this.processedItems = val;
        this.isLoadingData = false;
      },
    },
  },
  data() {
    return {
      items,
      isLoadingData: false,
      filter: "",
      filteredItems: items,
      processedItems: items,
      searchKey: "",
      sideBarCollapsed: false,
    };
  },
  created() {
    this.isLoadingData = true;
    EventEmitter.emitter.on("sidebar-toggle-sidebar", (value: boolean) => {
      this.sideBarCollapsed = value;
    });
  },
  methods: {
    numberOfType(type: string): number {
      const items = this.searchKey
        ? this.items.filter(
            (el: Block) => el.name.toUpperCase().indexOf(this.searchKey.toUpperCase()) > -1
          )
        : this.items;
      const i = items.filter((el: Block) => type === el.block_type);
      return Object.keys(i).length;
    },
    setSearchKey(key: string) {
      this.searchKey = key;
      this.searchBlock();
    },
    updateData(data: Block[]) {
      this.items = data;
      this.filteredItems = data;
      this.isLoadingData = false;
    },
    searchBlock() {
      const filtered = this.filteredItems.filter(
        (el: Block) => el.name.toUpperCase().indexOf(this.searchKey.toUpperCase()) > -1
      );
      this.processedItems = filtered;
    },
    addFilter(filter: string) {
      this.filter = this.filter === filter ? "" : filter;
      const filtered = this.items.filter((el: Block) => this.filter === el.block_type);
      this.filteredItems = this.filter !== "" ? filtered : this.items;
      this.processedItems = this.filteredItems;
      if (this.searchKey) this.searchBlock();
    },
  },
});
